import { Engine } from "./Core/Engine";

// 当DOM内容加载完成后执行
document.addEventListener('DOMContentLoaded', () => {
    // 初始化引擎
    Engine.Init();

    // 主循环
    function mainLoop() {
        Engine.Loop();
        // 请求下一帧动画
        requestAnimationFrame(mainLoop);
    }
    // 开始动画循环
    requestAnimationFrame(mainLoop);
});